import React, { useEffect, useState } from "react";
import { Card } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { firestore } from "../../firebase";
import { setOrders } from "../../redux/category-reducer";
import { Preloader } from "../preloader/Preloader";
import style from "./Shop.module.scss";

const ProdDetals = (props) => {
  const [loading, setLoading] = useState(true);
  const [product, setProduct] = useState();
  const [bought, setBought] = useState();
  const dispatch = useDispatch();

  const { orders } = useSelector(({ category }) => {
    return {
      orders: category.orders,
    };
  });

  useEffect(() => {
    const docRef = firestore.collection("product").doc(props.match.params.id);
    docRef
      .get()
      .then((doc) => {
        if (doc.exists) {
          // console.log("Document data:", doc.data());
          setProduct(doc.data());
          setLoading(false);
        } else {
          console.log("No such document!");
        }
      })
      .catch((error) => {
        console.log("Error getting document:", error);
      });
  }, []);

  useEffect(() => {
    if (orders.find((el) => el.id === props.match.params.id)) setBought(true);
    if (!orders.find((el) => el.id === props.match.params.id)) setBought(false);
  }, [orders]);

  const buy = (e) => {
    const item = { ...product, id: e.target.name, quantity: 1 };
    if (!localStorage.getItem("session_json")) {
      localStorage.setItem(
        "session_json",
        JSON.stringify({ purchases: [item] })
      );
      dispatch(setOrders([item]));
    } else {
      let arr = JSON.parse(localStorage.getItem("session_json")).purchases;
      if (arr.length >= 0) {
        if (!arr.find((el) => el.id == e.target.name)) {
          arr.push(item);
        }
      }
      localStorage.setItem("session_json", JSON.stringify({ purchases: arr }));
      dispatch(setOrders(arr));
    }
  };

  return (
    <div>
      {loading ? (
        <Preloader />
      ) : (
        <div className={style.prodDetals}>
          <img src={product.productImage ? product.productImage : null} />
          <div className={style.prodDescription}>
            <p>: {product.brendName}</p>
            <p>: {product.type}</p>
            <p> : {product.frameMaterial}</p>
            <p>: {product.price}</p>
            <Link className="btn btn-info" to={`/shop`}>
              
            </Link>
            {bought ? (
              <input
                className="float-right btn btn-success"
                name={props.match.params.id}
                type="button"
                value=" "
                disabled
              />
            ) : (
              <input
                className="float-right btn btn-success"
                name={props.match.params.id}
                type="button"
                value=""
                onClick={buy}
              />
            )}
          </div>
        </div>
        // <Card style={{ width: "95%", margin: "5px auto" }}>

        //   <Card.Img
        //     variant="top"
        //     src={product.productImage ? product.productImage : null}
        //   />
        //   <Card.Body>
        //     <Card.Text>: {product.brendName}</Card.Text>
        //     <Card.Text>: {product.type}</Card.Text>
        //     <Card.Text> : {product.frameMaterial}</Card.Text>
        //     <Card.Text>: {product.price}</Card.Text>
        //     <Link className="btn btn-info" to={`/shop`}>
        //       
        //     </Link>
        // <input
        //   className="float-right btn btn-primary"
        //   name={props.match.params.id}
        //   type="button"
        //   value=""
        //   onClick={buy}
        // />
        //   </Card.Body>
        // </Card>
      )}
    </div>
  );
};

export default ProdDetals;
